import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { srConfig, email } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';
import { socialMedia } from '@config';
const StyledContactSection = styled.section`
  max-width: 600px;
  margin: 0 auto 100px;
  text-align: center;

  @media (max-width: 768px) {
    margin: 0 auto 50px;
  }

  .overline {
    display: block;
    margin-bottom: 20px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: var(--fz-md);
    font-weight: 400;

    &:before {
      bottom: 0;
      font-size: var(--fz-sm);
    }

    &:after {
      display: none;
    }
  }

  .title {
    font-size: clamp(40px, 5vw, 60px);
  }

  .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
  }
`;
function SendMessage(props) {
  var link =  '';
  for (var i = 0; i < socialMedia.length; i++) { 

     if(socialMedia[i].name == '+Whatsapp'){ 
      var uri = ''+socialMedia[i].url+'?text=Necesito saber más';
        link = encodeURI(uri)
     }
   
  }  
  return <a className="email-link" target='_blank' href={link}>Enviar un mensaje vía +Whatsapp</a>  ;
}

const Contact = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <StyledContactSection id="contact" ref={revealContainer}>
      <h2 className="numbered-heading overline">¿Que sigue?</h2>

      <h2 className="title">Ponte en contacto</h2>

      <p>
        No tienes una idea clara de lo que quieres? no hay problema el acesoramiento de 15 minutos es gratuito, ponte en contacto con nosotros, le responderemos de inmediato.
      </p>
      <SendMessage />
      
    </StyledContactSection>
  );
};

export default Contact;
