import React, { useState, useEffect } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import styled from 'styled-components';
import { navDelay, loaderDelay } from '@utils';
import { usePrefersReducedMotion } from '@hooks';
import  Bghero from '../../assets/bghero.svg';
// import { email } from '@config';
import { Link } from "gatsby"

const StyledHeroSection = styled.section`
  ${({ theme }) => theme.mixins.flexCenter};
  flex-direction: column;
  align-items: flex-start;
  min-height: 100vh;
  padding: 0;

  @media (max-width: 480px) and (min-height: 700px) {
    padding-bottom: 10vh;
  }

  h1 {
    margin: 0 0 30px 4px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: clamp(var(--fz-sm), 5vw, var(--fz-md));
    font-weight: 400;

    @media (max-width: 480px) {
      margin: 0 0 20px 2px;
    }
  }

  h3 {
    margin-top: 10px;
    color: var(--slate);
    line-height: 0.9;
  }

  p {
    margin: 20px 0 0;
    max-width: 540px;
  }

  .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
  }
`;
const Bgcontainer = styled.div`
    position: absolute;
    right: 0;
    top: 19%;
    z-index: -1;
  @media (max-width: 768px) {
    opacity: 0.2;
  }
`;
const Blod = styled.div`
position: absolute;
z-index: -1;
`;
const Slash = styled.span`
margin-top: 5px;
display: inline-block;
vertical-align: middle;
`;
const Moneda = styled.span`
vertical-align: top;
font-size: 3rem;
`;
const Tiempo = styled.span` 
font-size: 1.2rem;
    vertical-align: middle;
    color: var(--slate);
    width: 116px;
    display: inline-block;
    margin-top: -15px;
    background: var(--green);
    padding: 14px 15px 7px 15px;
    border-radius: 15px;
    line-height: 1.3rem;
`;
const Hero = () => {
  const [isMounted, setIsMounted] = useState(false);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    const timeout = setTimeout(() => setIsMounted(true), navDelay);
    return () => clearTimeout(timeout);
  }, []);

  const one = <h1></h1>;
  const two = <h2 className="big-heading">Desde.</h2>;
  const three = <h3 className="big-heading"><Moneda>s</Moneda><Slash>/</Slash> 250 <Tiempo>Hasta Por +3 Años</Tiempo></h3>;
  const four = (
    <>
      <p>Para Mypes, Pymes ò negócios independientes,  Muestra lo que haces a tu público objetivo. La mejor forma de exponer tus productos o servicios en linea, todo a un bajo costo por más tiempo y con aspecto más profesional, no pagues por cosas que no necesitas{' '}
            </p>
    </>
  );
  const five = (
    <div>
    <Link
      className="email-link"
      to="/price/"
      activeStyle={{ color: "red" }}
      partiallyActive={true}
      >
    Contrata nuestro servicio Aqui!
    </Link>
    </div> 
  );

  const items = [one, two, three, four, five];

  return (
    <div> 
   
    <StyledHeroSection>
    <Bgcontainer><Bghero/></Bgcontainer>
      {prefersReducedMotion ? (
        <>
          {items.map((item, i) => (
            <div key={i}>{item}</div>
          ))}
        </>
      ) : (
        <TransitionGroup component={null}>
          {isMounted &&
            items.map((item, i) => (
              <CSSTransition key={i} classNames="fadeup" timeout={loaderDelay}>
                <div style={{ transitionDelay: `${i + 1}00ms` }}>{item}</div>
              </CSSTransition>
            ))}
        </TransitionGroup>
      )}
    </StyledHeroSection>
    </div>
  );
};

export default Hero;
